.email-show,
.email-edit {
  height: 500px;
}
.email-show {
  flex: 1;
  display: flex;
  justify-content: center;
}
.email-show .emails__thumb {
  width: 100%;
}
.email-edit .textarea__container label {
  display: none;
}

.email-show .emails__thumb .iframe-container {
  border-radius: 8px;
  background-color: var(--discount-input-bg-color);
  height: calc(100% - 52px);
}

.email-show .emails__thumb .iframe {
  height: 100%;
  width: 100%;
}
