.account-info-modal.info-modal {
  max-width: 313px !important;
}

.account-info-modal.info-modal .info-modal__row {
  font-size: 14px;
  min-height: 20px;
}

.account-info-modal.info-modal .info-modal__col1 {
  width: 40%;
  color: var(--white-color);
}

.account-info-modal.info-modal .info-modal__col2 {
  display: flex;
  justify-content: flex-end;
  font-weight: 700;
  text-align: end;
}

.account-info-modal__last-updated {
  display: flex;
  gap: 4px;
}

.account-info-modal__last-updated__time {
  color: var(--sideber-menu-color);
}

.metrics-sharing-modal.info-modal {
  max-width: 440px !important;
}

.metrics-sharing-modal__content {
  display: flex;
  flex-direction: column;
}

.metrics-sharing-modal__text {
  font-weight: 400;
  font-size: 16px;
  margin: 24px 0;
}

.metrics-sharing-modal__input label {
  font-weight: 600;
  font-size: 16px;
  line-height: 150%;
  padding-bottom: 12px;
}

.metrics-sharing-modal__input div {
  height: 50px;
}

.metrics-sharing-modal__input input {
  border: none;
  border-radius: 8px;
  font-weight: 600;
  font-size: 14px;
  color: var(--third-step-color);
}

.metrics-sharing-modal__input textarea {
  background-color: var(--discount-input-bg-color);
  color: var(--white-color);
  border: none;
}

.metrics-sharing-modal__input textarea:focus {
  background-color: var(--discount-input-bg-color);
  color: var(--white-color);
  border: none;
}

.metrics-sharing-modal__copy {
  background-color: #e2eaef;
  color: var(--main-black);
  width: 50px;
  border-radius: 8px;
}

.metrics-sharing-modal__share {
  margin-top: 24px;
  display: flex;
  justify-content: space-between;
}

.metrics-sharing-modal__share circle {
  fill: white;
}

.metrics-sharing-modal__share path {
  fill: var(--sidebar-bg-color);
}

.contact-modal__checkbox {
  display: flex;
  gap: 8px;
  align-items: center;
  color: var(--white-color);
  font-weight: 600;
  font-size: 14px;
  line-height: 150%;
  margin: 24px 0 30px 0;
}

.contact-modal__checkbox input {
  border-radius: 2px;
  outline: none;
  height: 24px;
  width: 24px;
  background-color: transparent;
}

.contact-modal__content {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.contact-modal__upload div {
  font-weight: 600;
  font-size: 14px;
  display: flex;
  align-items: center;
  gap: 8px;
}

.contact-modal__upload div i {
  font-size: 18px;
}

.contact-modal__upload input {
  display: none;
}

.trading-journal__container {
  padding: 30px;
}

.trading-journal__title {
  font-weight: 600;
  font-size: 16px;
  line-height: 150%;
  color: var(--white-color);
}

.trading-journal__no-data {
  border-radius: 8px;
  width: 100%;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--white-color);
  background: var(--discount-input-bg-color)
}

.trading-journal__select {
  width: 330px;
  max-width: 330px;
  margin: 24px 0;
}