.textarea__container label {
  font-weight: 600;
  font-size: 16px;
  line-height: 150%;
  padding-bottom: 12px;
}

.textarea__container div {
  height: 50px;
}

.textarea__container textarea {
  border: none;
  border-radius: 8px;
  font-size: 14px;
  background-color: var(--discount-input-bg-color);
  color: var(--white-color);

  border-radius: 8px;
  width: 100%;
  border: 0;
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 150%;
  padding: 14px 16px;
  box-shadow: var(--discount-input-shadows);
}

.textarea__container textarea {
  resize: none;
}
