.input-container {
  width: 100%;
  display: inline-flex;
  background: #2b2c31;
  border: 1px solid #494a4f;
  border-radius: 4px;
  height: 40px;
  align-items: center;
  overflow: hidden;
  background-color: var(--discount-input-bg-color);
}

.input-container input {
  flex: 1;
  background: #2b2c31;
  border: none;
  border-radius: 4px;
  padding-left: 15px;
  color: var(--white-color);
  box-sizing: content-box;
  border: none;
  border-radius: 8px;
  font-size: 14px;
  background-color: var(--discount-input-bg-color);
  color: var(--white-color);

  border-radius: 8px;
  width: 100%;
  border: 0;
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 150%;
  padding: 14px 16px;
  box-shadow: var(--discount-input-shadows);
}

.input-container .show-hide-icon {
  margin-right: 15px;
  color: #92929d;
  cursor: pointer;
}

.input-container input:-webkit-autofill,
.input-container input:-webkit-autofill:focus {
  transition: background-color 600000s 0s, color 600000s 0s;
  color: var(--white-color);
}

.light-input-container label {
  font-weight: 600;
  font-size: 16px;
  line-height: 150%;
  padding-bottom: 12px;
  color: var(--white-color);
}

.light-input-container div {
  height: 50px;
}

.light-input-container input {
  border: none;
  border-radius: 8px;
  font-size: 14px;
  background-color: var(--discount-input-bg-color);
  color: var(--white-color);

  border-radius: 8px;
  width: 100%;
  border: 0;
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 150%;
  padding: 14px 16px;
  box-shadow: var(--discount-input-shadows);
  /* color: var(--third-step-color); */
}

.select-container {
  position: relative;
}

.select-container select {
  border-radius: 8px;
  height: 50px;
  font-weight: 600;
  font-size: 14px;
  line-height: 150%;
  color: #2b3148;
}

.select-container__icon {
  background-color: #e2eaef;
  height: 24px;
  width: 24px;
  position: absolute;
  right: 0px;
  top: 0px;
  border-radius: 50%;
  margin: 13px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.select-container__icon i {
  font-size: 12px;
}
