.expand-collapse-icon-button.MuiIconButton-root {
    height: 24px;
    width: 24px;
    background-color: var(--white-color);
    color: var(--discount-input-bg-color);
    transition: all .3s;
}

.expand-collapse-icon-button.MuiIconButton-root:hover {
    background-color: var(--white-color);
    color: var(--discount-input-bg-color);
}

.expand-collapse-icon-button.MuiIconButton-root.open {
    transform: rotate(180deg);
}