/* Login Box */

.trial__login__box {
    background: var(--sidebar-bg-color);
    /* padding: 30px; */
    border-radius: 8px;
    box-shadow: 0px 4px 40px rgba(137, 137, 137, 0.08);
}

.login__code {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 85px;
    padding: 0px 30px;
}

.login__code p {
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 150%;
    display: flex;
    align-items: center;
    color: var(--white-color);
    gap: 10px;
    margin: 0;
}


.login__box__body {
    padding: 0px 30px 30px 30px;
}

.balance__content p {
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    align-items: center;
    color: var(--sideber-menu-color);
    margin-bottom: 30px;
}

.balance__content p .white_color {
    color: var(--white-color);
}

.balance__content .orange_color {
    color: #ffbe08;
}

.trial__btn {
    display: flex;
}

.trial__btn a,
.trial__btn div {
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 150%;
    display: flex;
    align-items: center;
    color: #2b3148;
    border: 1px solid #242424;
    width: 130px;
    height: 42px;
    background: #e2eaef;
    border-radius: 8px;
    justify-content: center;
    margin-right: 14px;
}

.trial__btn a img,
.trial__btn div img {
    margin-right: 8px;
}