.become-an-affiliate__form {
  background-color: var(--your-order-table-color);
  border-radius: 14px;
  width: 100%;
  padding: 30px;
}

.become-an-affiliate__form__input {
  display: flex;
  flex-wrap: wrap;
  gap: 8px 64px;
}

.become-an-affiliate__form__input div {
  max-width: 400px;
}

.become-an-affiliate__form button {
  width: 100%;
  max-width: 367px;
  height: 60px;
  margin-top: 35px;
}

.payout-form__available input {
  font-weight: 600;
}
