.profile-select {
    height: 50px;
    background: var(--discount-input-bg-color);
    border-radius: 8px;
    width: 100%;
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 150%;
    color: var(--white-color);
    padding: 0px 0px;
    box-shadow: var(--discount-input-shadows);
    border: none;
}

.custom__autocomplete .MuiAutocomplete-inputRoot {
    height: 50px;
    padding: 0 65px 0 9px !important;
}

.profile-select fieldset,
.custom__autocomplete fieldset {
    border: none;
}