.affiliate-empty-container {
  background-color: var(--your-order-table-color);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 14px;
  width: 100%;
  padding: 30px;
  gap: 28px;
  margin-top: 40px;
  font-family: 'Work Sans';
  font-weight: 600;
  font-size: 16px;
}

.affiliate-empty-container__title {
  line-height: 140%;
  color: var(--white-color);
  align-self: flex-start;
}

.affiliate-empty-container-icon {
  background-color: var(--history-td-bg-color);
  height: 52px;
  width: 52px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.affiliate-empty-container-icon span {
  font-size: 16px;
  font-weight: 600;
  line-height: 16px;
  color: #338ae2;
}

.affiliate-empty-container__text {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 140%;
  color: #98a5b2;
}

.affiliate-empty-container button {
  background: linear-gradient(107.13deg, #5ebbff -9.78%, #0066ff 130.59%);
  border-radius: 8px;
  width: 215px;
  height: 50px;
  font-weight: 600;
  line-height: 140%;
  color: var(--white-color);
  text-align: center;
  outline: unset !important;
  border: unset !important;
  margin-bottom: 20px;
}
