.blue__linear__btn {
    width: 100%;
    background: linear-gradient(90deg, #1b5aa0 0%, #0080b7 100%);
    border-radius: 4px;
    height: 50px;
    color: var(--white-color);
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 500;
    font-size: 14px;
    line-height: 0px;
    cursor: pointer;
    outline: unset !important;
    border: unset !important;
    box-shadow: var(--discount-input-shadows);
    font-family: 'Open Sans';
}

.blue__linear__btn.large-60 {
    height: 60px;
    border-radius: 8px;
    font-weight: 600;
    font-size: 16px;
    line-height: 150%;
    color: #ffffff;
}

.blue__linear__btn:disabled {
    cursor: not-allowed;
    opacity: 0.9;
}

.blue__linear__btn.linear-bg-1 {
    background: linear-gradient(90deg, #1b5aa0 0%, #0080b7 100%);
}

.blue__linear__btn.linear-bg-2 {
    background: linear-gradient(94.69deg, #005EC7 -7.47%, #0080B7 109.53%);
    border-radius: 8px;
}

button.secondary__btn {
    border: 0px;
    background-color: transparent;
    outline: unset;
    cursor: pointer;
}

button.secondary__btn {
    font-family: 'Anek Bangla';
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 150%;
    color: #ffffff !important;
    position: relative;
    z-index: 1;
    transition: 0.3s all;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 8px;
    padding: 5px 18px;
    width: fit-content;
}

button.secondary__btn::before {
    background-image: url('./secondary_btn_bg.png');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    transition: 0.3s all;
    z-index: -1;
    border-radius: 8px;
}

@media only screen and (min-width: 320px) and (max-width: 767px) {
    .blue__linear__btn {
        height: 40px !important;
        font-size: 12px !important;
    }

    button.secondary__btn {
        height: 46px;
    }
}