.info-modal-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: rgba(0, 0, 0, 0.3);
}

.info-modal {
  margin: 24px;
  position: relative;
  width: 100%;
  max-width: 542px;
  background: var(--sidebar-bg-color);
  border-radius: 8px;
  outline: none;
  padding: 30px 30px 60px 30px;
  color: var(--white-color);
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.info-modal__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.info-modal__header__title {
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 150%;
}

.info-modal__header__close {
  margin: 12px;
}

.info-modal__header__close img {
  cursor: pointer;
  height: 24px;
  width: 24px;
}

.info-modal__header__close .img-dark {
  filter: brightness(0) invert(1);
}

.info-modal__divider {
  width: 100%;
  height: 1px;
  background-color: #dbdbdb;
}

.info-modal__row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-height: 40px;
  font-weight: 600;
  font-size: 16px;
  line-height: 150%;
}

.info-modal__col1 {
  width: 30%;
  color: #d1d1d1;
}

.info-modal__col2 {
  flex: 1;
}

.info-modal__col2 a {
  color: #00a4dd;
}

.icon-button-copy {
  cursor: pointer;
  background-color: var(--white-color);
  width: 40px;
  height: 40px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.icon-button-copy i {
  color: var(--main-black);
}