.collapse__container {
    width: 100%;
    background: var(--discount-input-bg-color);
    width: 100%;
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 150%;
    color: var(--white-color);
    padding: 0 16px;
    box-shadow: var(--discount-input-shadows);
    border-radius: 8px;
}

.collapse__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 50px;
}

.collapse__header .icon-button {
    height: 24px;
    width: 24px;
    background-color: var(--white-color);
    color: var(--discount-input-bg-color);
    transition: all .3s;
}

.collapse__header .icon-button:hover {
    background-color: var(--white-color);
    color: var(--discount-input-bg-color);
}

.collapse__header .icon-button.open {
    transform: rotate(180deg);
}

.collapse__body {
    padding-bottom: 24px;
}